<template lang="html">
    <div class="header">
        <span class="left" @click="reback()">取消</span>
        <span>动态</span>
        <span @click="Post">发表</span>
    </div>
</template>

<script>
export default {
    name: 'GHeader',
    props: {
        hname: {
            default: 'admin'
        }
    },
    methods: {
        Post (){
            this.$emit('post');
            console.log('被触发');
        },
        reback (){
            this.$router.push('/?username'+this.hname);
            console.log('返回了');
        }
    }
}
</script>

<style scoped>
    .header {
        height: 48px;
        width: 100%;
        background-color: #12b7f5;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-left: 16px;
        padding-right: 16px;
    }
    .header span {
        display: block;
        color: #fff;
        font-size: 16px;
        font-weight: 500;
    }
</style>



